<template>
  <x-header :left-options="{showBack: false}"  :right-options="{showMore: true}"  @on-click-more="showMenus = true"></x-header>
  <group>
    <template v-for="item in items">
      <cell :title="item.name" :inline-desc="item.location" is-link :link="getlink(item.id)"> {{ item.price }}</cell>
    </template>
  </group>
  <divider></divider>
  <divider></divider>
  <actionsheet :menus="menus" :show.sync="showMenus" show-cancel @on-click-menu="click"></actionsheet>
  <alert :show.sync="show" title="" button-text="">
    <p style="text-align:center;"><br>noahgaocn@gmail.com</p>
    <p style="text-align:center;"><br><br><br><br>TUSTDX</p>
  </alert>
</template>

<script>
import { Cell, XHeader, Actionsheet, Alert, Divider, Group } from 'vux/src/components'

export default {
  components: {
    Cell,
    XHeader,
    Actionsheet,
    Alert,
    Divider,
    Group
  },
  data () {
    return {
      menus: {
        menu1: '',
        me: '',
        menu2: ''
      },
      showMenus: false,
      show: false,
      items: []
    }
  },
  created () {
    let Items = window.Bmob.Object.extend('items')
    let query = new window.Bmob.Query(Items)
    query.equalTo('status', 0)
    // 
    query.find().then((results) => {
      let temp = []
      console.log(' ' + results.length + ' ')
      for (let i = 0; i < results.length; i++) {
        let object = results[i]
        let tmp = {
          id: object.id,
          name: object.get('name'),
          location: object.get('location'),
          price: object.get('price')
        }
        temp.push(tmp)
      }
      this.items = temp
    }, (error) => {
      console.log(': ' + error.code + ' ' + error.message)
    })
  },
  methods: {
    click (key) {
      if (key === 'menu1') {
        this.$router.go('/new')
      } else if (key === 'me') {
        this.$router.go('/user')
      } else if (key === 'menu2') {
        this.show = true
        console.log('Show Feedback')
      } else {
        console.log(key)
      }
    },
    getlink (id) {
      return '/detail/' + id
    }
  }
}
</script>


